import { Typography } from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

const { Title } = Typography;
const CPageTitle = ({ title }) => {
  const router = useRouter();
  const [titlePage, setPageTitle] = useState("Add");
  useEffect(() => {
    let path = router.pathname.slice(1);
    path = path.replaceAll("/[id]", "");
    path = path.replaceAll("/", " / ");
    path = path.replaceAll("-", " ");
    let toArray = path.split(" ");

    let toCapitalize = toArray
      .map((word) => {
        return word[0].toUpperCase() + word.substring(1);
      })
      .join(" ");
    setPageTitle(toCapitalize);
  }, []);

  return (
    <Title style={{ marginBottom: 25 }} level={3}>
      {titlePage}
      {title && " > "}
      {title && <span style={{ color: "#00000073" }}>{title}</span>}
    </Title>
  );
};

export default CPageTitle;
